<template>
    <div>
        <div>
            {{teamSelected}}
        </div>
        <button @click="startGame()">Start game</button>
    </div>
</template>

<script>
export default {
    computed:{
        teamSelected: function(){
            return this.$store.getters['user/TeamSelection']
        } 
    },
    methods:{
        startGame(){
            let send = {
                code: -1,
                gid: null,
                data: null
            }
            this.$store.getters["socket/socket"].send(JSON.stringify(send))
        }
    }
}
</script> 